@import url('https://fonts.googleapis.com/css2?family=Chivo&family=Josefin+Sans&family=Podkova&display=swap');


body{
    font-family: 'Chivo', sans-serif;
    background-color: #f1f2f6;
    text-align: center;
    letter-spacing: 1px;
    color: black;

}

h1 {
    color: #5a189a;
    font-size: 10vw;
    margin: 1rem 0.5rem;
    font-family: 'Josefin Sans', sans-serif;
}

h2 {
    color: #5a189a;
    font-size: 4vw;
    margin: 0.5rem;
}

textarea{
    width: 60vw;
    height: 30px;
    padding: 0.5rem;
    border-radius: 12px;
    border: 2px solid rgba(0,0,0,0.2);
    box-shadow: 0px 0px 10px 1px rgb(0, 0, 0, 0.1);
    resize: vertical;
    font-family: 'Chivo', sans-serif;
}

textarea:focus{
    outline: transparent;
}

form{
    display: flex;
    justify-content:space-evenly;
    align-items: center;
    width: 80vw;
    margin: auto;

}

.modal {
    position: fixed;
    background-color: rgba(0,0,0,0.4);
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
}

.modal-content{
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80vw;
    animation: animatetop 0.5s;
    border-radius: 12px;

}

@keyframes animatetop {
  from {top: -300px; opacity: 0}
  to {top: 0; opacity: 1}
}

.close{
   color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold; 
}

.close:hover,
.close:focus {
  color: black;
  cursor: pointer;
}

.todo-icon {
    font-size: 28px;
    margin: 0.5rem;
    visibility: hidden;
}


ul {
    padding: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    width: 80vw;
    margin: auto;
}

li{
    list-style: none;
    font-family: 'Chivo', sans-serif;
}

pre{
    white-space: pre-wrap;
}

.note-container{
    background-color: white;
    border: 1px solid rgb(0, 0, 0, 0.2);
    box-shadow: 0px 0px 5px 1px rgb(124, 50, 237, 0.5);
    padding: 0.5rem;
    margin: 1rem 0;
    width: 300px;
    border-radius: 12px;
    position: relative;
    transform: scale(0.98);
}

.note-container:hover{
    transform: scale(1);
    transition: all 0.3s;
}

.note-container:hover .todo-icon {
    visibility: visible;
}

.note-actions button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: #5a189a;
}

.btn-addNote,
.btn-updateNote {
    background-color: white;
    border: none;
    height: 50px;
    width: 60px;
    border-radius: 12px;
    font-size: 48px;
    line-height: 50px;
    box-shadow: 0px 0px 10px 1px rgb(0, 0, 0, 0.1);
    cursor: pointer;
    color: #5a189a;
}

.footer {
    position: fixed;
    bottom: 0px;
    left: 0px;
    font-size: 12px;
    writing-mode:vertical-rl;
    text-orientation: sideways-right;
    transform: rotate(180deg);
    margin-bottom: 1rem;
}

@media (min-width:501px) and (max-width:800px) {
    .note-container{
        width: 80vw;
    }
}

@media (max-width:500px) {
    h1{
        font-size: 15vw;
    }
}